import React, { Component } from "react";
import "../../styles/congratulation.css";
import tickIcon from "../../assets/images/tick1.svg";
import { connect } from "react-redux";
import history from "../../routes/History";
import birballogo from "../../assets/images/instructions/birballogo.png";
import ReactGA from "react-ga";

export class Congratulation extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loginDetails: "",
    };
  }
  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    // localStorage.clear();
    window.onbeforeunload = null;


  }
  render() {
    return  <div className="congraluation-main">
        <div className="birbalLogoCongo">
          <img className="img-fluid" src={birballogo}></img>
        </div>
        <div className="mainCircle">
          <div className="circle">
            <div className="circle2">
              <div className="tick-icon-div">
                <img src={tickIcon} />
              </div>
            </div>
          </div>
          <div className="congratualtion-text">
            Response Submitted successfully,
          </div>
          <div className="congratualtion-text">
            {" "}
            If shortlisted you may be contacted from HR Team!
          </div>
        </div>
      </div>
 
  }
}

const mapStateToProps = (state) => {
  return {};
};
const mapDispatchToProps = (dispatch) => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(Congratulation);
